const mutations = {
	closeNotice(state) {
		uni.setStorageSync('noticemd5', state.config.noticemd5)
		state.isNotice = false
	},
	addConfig(state, data) {
		state.config = { ...state.config,
			...data
		}
	},
	setUrl(state, url) {
		state.url = url
	},
	setSys(state, data) {
		state.sys = Object.assign(state.sys, data)
	}
}

export default {
	namespaced: true,
	state,
	getters,
	actions,
	mutations
}
